
import Vue from 'vue';
import {Layout} from 'vue-ydui/dist/lib.rem/layout';
Vue.component(Layout.name, Layout);
import MescrollVue from 'mescroll.js/mescroll.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'JFFrom',
components: {
XHeader,
MescrollVue
},
data() {
return {
validity: {},
valid: undefined,
model: {
identity: '',
name: '',
number: '',
phone: '',
checkboxGroupValue:[],
uploadValue: []
},
schema:{
fields:[

{
type:'input',
modelKey:'identity',
label:'',
props:{
placeholder:', ',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:''

}
},
{
type:'input',
modelKey:'name',
label:'',
props:{
placeholder:'',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:''

}
},
{
type:'input',
modelKey:'number',
label:'',
props:{
placeholder:'',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:''

}
},
{
type:'input',
modelKey:'phone',
label:'',
props:{
placeholder:'',
},
rules:{

required:true,
type:'number'

},
trigger: 'blur',
messages:{
required:''

}
},
{
type: 'checkbox-group',
modelKey: 'checkboxGroupValue',
label: '',
props: {
options: ['', '', '','','']
},
rules: {
required: true
},
trigger: 'blur',
messages:{
required:''

}
},
{
type: 'upload',
modelKey: 'uploadValue',
label: '',
events: {
'file-removed': (...args) => {
console.log('file removed', args)
}
}


},
{
type:'submit',
label:''
},
{
type:'reset',
label:''
}
]
}

}
},
methods: {
submitHandler(e) {
e.preventDefault()
try{
var a,b,c,d
a = this.model.checkboxGroupValue
b= a.join(";")
c= this.model.uploadValue
d= c.join("|")
console.log(b)
console.log(d)
var token=window.localStorage.getItem("access_token")
this.$axios({method: 'post',url: '/api/v1/visit?access-token='+token,data:
{identity: this.model.identity,name: this.model.name,
number: this.model.number,phone: this.model.phone,
content: b,org_id: 1,pics: d,}}).then((res)=>{
if(res.code=='200'){
this.$dialog.alert({mes: ''})

}else{
alert(res.data.message)
}
})

}catch(err){
console.log(err)
}

},
resetHandler(e) {
console.log('reset', e)
}
}
}



