
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'Login',
components: {
XHeader
},
data(){
return{
model:{
username:'',
password:''
},
schema:{
fields:[

{
type:'input',
modelKey:'username',
label:'',
props:{
placeholder:'',
},
rules:{

required:true,
type:'string',
min:3,
max:15,
},
trigger: 'blur',
messages:{
required:'',
min:'3',
max:'15'
}
},

{
type:'input',
modelKey:'password',
label:'',
props:{
placeholder:'',
type:'password',
eye:{
open:false,
}
},
rules:{
required:true,
},
trigger:'blur',
},
{
type:'submit',
label:''
}
]
}
}
},
methods:{
submitHandler(e){
e.preventDefault()

try{
this.$axios({method: 'post',url: '/api/v1/site/login',data:
{username: this.model.username,password: this.model.password,group:'app'}}).then((res)=>{
if(res.code=='200'){
this.$store.commit('settoken',res.data.access_token)
window.localStorage.setItem('access_token',res.data.access_token)
this.$store.commit('setrealname',res.data.member.realname)
window.localStorage.setItem('realname',res.data.member.realname)
this.$store.commit('setmobile',res.data.member.mobile)
window.localStorage.setItem('mobile',res.data.member.mobile)
this.$store.commit('setemail',res.data.member.email)
window.localStorage.setItem('email',res.data.member.email)
this.$store.commit('setorgname',res.data.member.org_name)
window.localStorage.setItem('org_name',res.data.member.org_name)
this.$router.push({ path: '/home' })
}else{
alert(res.data.message)
}
})

}catch(err){
console.log(err)
}
}
}

}



