
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'Grxx',
components: {
XHeader
},
data(){
return{

model:{
name: window.localStorage.getItem("realname"),
email: window.localStorage.getItem("email"),
phone: window.localStorage.getItem("mobile")
},
schema:{
fields:[

{
type:'input',
modelKey:'name',
label:'',
props:{
placeholder:'',
},
rules:{

required:true,
type:'string',
min:2,

},
trigger: 'blur',
messages:{
required:'',
min:'2',

}
},

{
type:'input',
modelKey:'phone',
label:'',
props:{
placeholder:'',
type:'number',

},
rules:{
required:true,
type:'number',
},
trigger:'blur',
messages:{
required:'',
type:''

}
},
{
type:'input',
modelKey:'email',
label:'',
props:{
placeholder:'',

},
rules:{
required:true,
type:'string',
},
trigger:'blur',
messages:{
required:'',

}
},
{
type:'submit',
label:''
}
]
}
}
},
methods:{
submitHandler(e){
e.preventDefault()
try{
var id = 5186
var token=window.localStorage.getItem("access_token")
this.$axios({
method: 'put',
url:'/api//v1/member/5186?access-token=' +token,
data: {
realname: this.model.name,
mobile: this.model.phone,
email: this.model.email

}
}).then((res)=>{

if(res.code=='200'){
this.$dialog.alert({mes: ''});
}else{
this.$dialog.alert({mes: ''});
}
})

}catch(err){
console.log(err)
}

}
}

}



